import { curveCatmullRom } from 'd3-shape'
import '../../node_modules/react-vis/dist/style.css'
import {
  XYPlot,
  VerticalBarSeries,
  VerticalGridLines,
  HorizontalGridLines,
  YAxis,
  XAxis,
  DiscreteColorLegend
} from 'react-vis'
import _ from 'lodash'
import Title from '../styles/Title'

export default class Chart extends React.Component {
  render () {
    // const data = [{ x: 0, y: 8 }, { x: 1, y: 5 }, { x: 2, y: 3 }]

    let d = _.groupBy(this.props.data.contracts, 'currentStatus.name')
    let data = _.values(d).map((value, index) => {
      return {
        x: value[0].currentStatus.name,
        y: value.length,
        color: value[0].currentStatus.color,
        opacity: 0.7
      }
    })

    let legend = _.values(d).map((value, index) => {
      return {
        title: value[0].currentStatus.name,
        color: value[0].currentStatus.color
      }
    })

    return (
      <div className='pa3 f3'>
        <Title text='Charts' />
        <XYPlot xType='ordinal' height={600} width={600} colorType='literal'>
          <DiscreteColorLegend
            style={{ position: 'absolute', left: '40px', top: '0px' }}
            orientation='horizontal'
            items={legend}
          />
          <VerticalGridLines />
          <HorizontalGridLines />
          <XAxis />
          <YAxis />
          <VerticalBarSeries data={data} />
        </XYPlot>

      </div>
    )
  }
}



